.NavBarItems{
    position: fixed;
    background: #56c8e7;
    height:80px;
    width: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
   
}

.navbar-logo{
    color:#fff;
    justify-self: start;
    margin-left: 20px;
    cursor:pointer;

}
.fa-react{
    margin-left: 0.5rem;
    font-size: 1.6rem;
}
.nav-menu{
    display:grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem
}
.nav-links{
    color: white;
    text-decoration: none;
    padding:0.5rem 1rem;
}
.nav-links:hover{
    background-color:#217aa2;
    border-radius: 4px;
    transition: all 0.2s ease-out; 
}
.fa-bars{
    color: white
}
.nav-links-mobile{
    display: none;
}
.menu-icon{
    display:none;
}
@media screen and (max-width:960px){
    .NavBarItems{
        position:relative;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top:80px;
        left: -800px;
        opacity: 1;
        transition: all 0.5s ease;

    }
    .nav-menu.active{
        background: #217aa2;
        left:0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    .nav-links{
        text-align:center;
        padding:2rem;
        width: 100%;
        display:table;

    }
    .nav-links:hover{
        background-color: #4096bc;
        border-radius: 0;

    }
    .navbar-logo{
        position: absolute;
        top:0;
        left:0;
        transform: translate(25% 50%);
    }
    .menu-icon{
        display: block;
        position: absolute;
        top:0;
        right: 0;
        transform: translate(-100%,60%);
        font-size: i.8rem;
        cursor: pointer;
    }
    .fa-times{
        color:#fff;
        font-size:2rem;
    }
    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin:2rem auto;
        border-radius: 4px;
        width:80%;
        background:#08c1f6;
        text-decoration: none;
        color:#000;
        font-size: 1.5rem;



    }
    .nav-links-mobile:hover{
        background: #18809e;
        color:#000;
        transition:250ms;

    }
    Button{
        display:none;
    }
}
